<template>
  <div>
    <img :src="item.imgUrl" v-for="(item, index) in baseLayers" :key="index" @click="changeBaseLayer(item)">
  </div>
</template>

<script>
import { baseLayers } from "./../../base/config.js";
export default {
  name: "",

  data() {
    return {
      baseLayers: baseLayers
    };
  },

  methods: {
    changeBaseLayer(item) {
      this.$emit("changeLayer", item);
    }
  }
};
</script>

