<template>
  <div>
    <div id="map" class="map">
    </div>
  </div>
</template>

<script>
import { Map, View } from "ol";
import TileLayer from "ol/layer/Tile";
import TileGrid from "ol/tilegrid/TileGrid";
import TileImage from "ol/source/TileImage";
import OSM from "ol/source/OSM";
import XYZ from "ol/source/XYZ";
import MultiTileLayer from "./../plugin/MultiTileLayer";
import BaseLayerPanel from "./../../Public/BaseLayerPanel";
import { mapconfig } from "./../../../base/config.js";
export default {
  name: "",
  props: [],
  data() {
    return {
      tilegrid: null
    };
  },
  components: { BaseLayerPanel },
  mounted() {
    // 自定义分辨率和瓦片坐标系
    var resolutions = [];
    var maxZoom = 18;
    // 计算百度使用的分辨率
    for (var i = 0; i <= maxZoom; i++) {
      resolutions[i] = Math.pow(2, maxZoom - i); //(幂运算)
    }
    this.tilegrid = new TileGrid({
      // origin: [43.88955327932, 12.590178885765], // 设置原点坐标
      origin: [0, 0], // 设置原点坐标
      resolutions: resolutions // 设置分辨率
    });

    //谷歌行政区划
    var googleMapLayer = new TileLayer({
      source: new XYZ({
        url:
          "http://www.google.cn/maps/vt/pb=!1m4!1m3!1i{z}!2i{x}!3i{y}!2m3!1e0!2sm!3i345013117!3m8!2szh-CN!3scn!5e1105!12m4!1e68!2m2!1sset!2sRoadmap!4e0"
      })
    });
    //谷歌影像
    var googleRasterLayer = TileLayer({
      source: new TileImage({
        url:
          "http://mt2.google.cn/vt/lyrs=y&hl=zh-CN&gl=CN&src=app&x={x}&y={y}&z={z}&s=G"
      })
    });

    var vm = this;
    var xyzLayer = new XYZ({
      url: mapconfig.baseLayer_ol
    });

    var map = new Map({
      target: "map",
      layers: [
        // new TileLayer({
        //   source: new OSM()
        // })
        new TileLayer({
          source: vm.getBaiduSourceLabel()
        }),
        new TileLayer({
          source: vm.getBaiduSourceRaster()
        })
        // new TileLayer({
        //   source: vm.getBaiduSource() //xyzLayer
        // })
      ],
      view: new View({
        projection: mapconfig.projection,
        center: mapconfig.center,
        zoom: mapconfig.zoom
      })
    });
  },

  methods: {
    getBaiduSource() {
      // 创建百度地图的数据源
      var baiduSource = new TileImage({
        projection: "EPSG:3857",
        tileGrid: this.tilegrid,
        tileUrlFunction: function(tileCoord, pixelRatio, proj) {
          var z = tileCoord[0];
          var x = tileCoord[1];
          var y = tileCoord[2];

          // 百度瓦片服务url将负数使用M前缀来标识
          if (x < 0) {
            x = "M" + -x;
          }
          if (y < 0) {
            y = "M" + -y;
          }

          return (
            "http://online0.map.bdimg.com/onlinelabel/?qt=tile&x=" +
            x +
            "&y=" +
            y +
            "&z=" +
            z +
            "&styles=pl&udt=20160426&scaler=1&p=0"
          );
        }
      });

      return baiduSource;
    },
    getBaiduSourceRaster() {
      // 百度影像
      var baiduSourceRaster = new TileImage({
        tileGrid: this.tilegrid,
        tileUrlFunction: function(tileCoord, pixelRatio, proj) {
          var z = tileCoord[0];
          var x = tileCoord[1];
          var y = tileCoord[2];

          // 百度瓦片服务url将负数使用M前缀来标识
          if (x < 0) {
            x = "M" + -x;
          }
          if (y < 0) {
            y = "M" + -y;
          }
          return (
            "http://shangetu" +
            parseInt(Math.random() * 10) +
            ".map.bdimg.com/it/u=x=" +
            x +
            ";y=" +
            y +
            ";z=" +
            z +
            ";v=009;type=sate&fm=46&udt=20170606"
          );
        }
      });
      return baiduSourceRaster;
    },
    getBaiduSourceLabel() {
      // 百度标注
      var baiduSourceLabel = new TileImage({
        tileGrid: this.tilegrid,
        tileUrlFunction: function(tileCoord, pixelRatio, proj) {
          var z = tileCoord[0];
          var x = tileCoord[1];
          var y = tileCoord[2];

          // 百度瓦片服务url将负数使用M前缀来标识
          if (x < 0) {
            x = "M" + -x;
          }
          if (y < 0) {
            y = "M" + -y;
          }
          return (
            "http://online" +
            parseInt(Math.random() * 10) +
            ".map.bdimg.com/onlinelabel/?qt=tile&x=" +
            x +
            "&y=" +
            y +
            "&z=" +
            z +
            "&styles=sl&udt=20170620&scaler=1&p=1"
          );
        }
      });
      return baiduSourceLabel;
    }
  }
};
</script>
